body {
  background-color: #32455e;
  color: rgb(219, 217, 217);
  font-family: 'DM Serif Display', serif;
}

h1, h2, h3 {
  color:rgb(17, 21, 26);
  font-weight: 400;
}

ul {
  list-style-type: none;
}

a {
   text-decoration: none;
   color: rgb(219, 217, 217);
}

button {
  cursor: pointer;
  border: none;
}

img {
  max-width: 100%;
  height: auto;
}

.section {
  padding: 0.5rem 0rem 2.8rem 0rem;
  margin: -1.3rem 2rem 2rem 2rem;
  border: 2px solid rgb(92, 113, 138);
  border-radius: 2.5rem;
}

.section_title {
  color:rgb(17, 21, 26);
  font-size: 3.5rem;
  margin-top: 0.2rem;
}

.section_subtitle {
  display: block;
  margin-bottom: 4rem;
}

.section_title,
.section_subtitle {
  text-align: center;
}

.container {
  max-width: 968px;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.button {
  display: inline-block;
  background-color: whitesmoke;
  color: rgb(17, 21, 26);
  padding: 0.8rem 2rem;
  border-radius: 3rem;
  font-weight: 600;
  margin-top: 0.7rem;
  margin-bottom: 1.5rem;
}

.button:hover {
  background-color: rgb(92, 113, 138);
  color: whitesmoke;
  transition: .3s;
  font-weight: 400;
}

.button_icon {
  margin-left: 0.3rem;
  color: rgb(17, 21, 26);
}

.button--flex {
  display: inline-flex;
  align-items: center;
}





