.contact_container{
    grid-template-columns: repeat(2, -webkit-max-content);
    grid-template-columns: repeat(2, max-content);
    display:  grid;
    justify-content: center;
    /* column-gap: -9rem; */
    padding: 6rem 0rem 6rem 0rem;
    /* margin-left: 10.5rem; */
}

.contact_title{
    text-align: center;
    font-size: 2.2rem;
    font-weight: 400;
    margin-bottom: 1rem;
}

h2.section_title{
    margin-top: 3rem;
}

.section_subtitle{
    margin-top: 0.2rem;
    margin-bottom: 3.5rem;
}

.contact_info{
    display: grid;
    grid-template-columns: 400px;
}

.contact_content{
    /* margin-left: 2rem; */
    grid-template-columns: 400px;
}

.contact_card{
    background-color: rgb(92, 113, 138);
    border-radius: 1.2rem;
    padding: 3rem 0.5rem;
    margin-top: 1rem;
    text-align: center;
    height: 230px;
    width: 350px;
}



.contact_card-icon{
    font-size: 2rem;
    margin-bottom: 0.2rem;
}

.contact_card-title,
.contact_card-data{
    font-size:1.5rem;
}

.contact_card-title{
    font-weight: 400;
}

.contact_card-data{
    display: block;
    margin-bottom: 0.7rem;
}

.contact_button{
    color: rgb(17, 21, 26);
    font-size: 1.2rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    grid-column-gap: 0.3rem;
    -webkit-column-gap: 0.3rem;
            column-gap: 0.3rem;
}

.contact_button-icon{
    font-size: 1rem;
    transition: 0.3s;
}

.contact_button:hover .contact_button-icon{
    -webkit-transform: translate(0.25rem);
            transform: translate(0.25rem);
}

.contact_form{
    width: 500px;
}

.contact_form-div{
    position: relative;
    margin-bottom: 1.5rem;
    height: 4rem;
}

.contact_form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: none;
    color: rgb(17, 21, 26);
    outline: none;
    border-radius: 1rem;
    border: solid 2px #DBD9D9;
    padding: 1.5rem;
    z-index: 5;
}

.contact_form-tag{
    position: absolute;
    top: -0.7rem;
    left: 1.2rem;
    font-size: 1rem;
    padding: 0.3rem;
    background-color: #32455e;
    border-radius: 0.5rem;
    z-index: 10;
}

.contact_form-area{
    height: 11rem;
}

.contact_form-area textarea{
    resize: none;
}

.message{
    text-align: right;
    padding: 6%;
    color: rgb(17, 21, 26);
}



  @media screen and (max-width: 992px){
    .contact_container{
        /* margin-left:10%; */
        grid-template-columns: none;
    }
    .contact_title{
        visibility: hidden;
        margin-top: -2rem;
        margin-bottom: -2rem;
    }
    .contact_content{
        margin-top: -5rem;
    }
    .contact_card{
        margin-bottom: -2rem;
        margin-left: 10%;
    }
  }
  
  @media screen and (max-width: 768px){
    /* .contact_container{
        row-gap: 3rem;
    } */

    .contact_info{
        justify-content: center;
    }

    .contact_form{
        margin: 0 auto;
        width: 30rem;
    }
  }
  
  @media screen and (max-width: 576px){
    /* .contact_info{
        grid-template-columns: none;
    }
    .contact_card{
        margin-left: 0;
    } */
    .contact_section{
        justify-content: center;
        text-align: center;
    }
    .contact_card{
        width: 20rem;
    }
    .contact_content h2{
        font-size: 2.3rem;
    }
    .contact_card-title, .contact_card-data{
        font-size: 1.4rem;
    }
  }
  
  @media screen and (max-width: 420px){
    .contact_card{
        width: 18rem;
        margin-left: 15%;
    }
    .contact_card-title, .contact_card-data{
        font-size: 1.2rem;
    }
  }
.footer {
    background-color: rgb(17, 21, 26);
    /* border: 2px solid rgb(92, 113, 138); */
}

.footer_container{
    padding: 1.2rem 0 4rem;
}

.footer_title,
.footer_link{
    color: rgb(92, 113, 138);
}

.footer_title{
    text-align: center;
    margin-bottom: 1.5rem;
}

.footer_link{
    font-size: 1.2rem;
}

span.footer_copy{
    text-align: center;
    font-size: 1rem;
}

.footer_link:hover{
    color: white;
    transition: .3s;
}

.footer_list{
    display: flex;
    justify-content: center;
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
    margin-bottom: 1.3rem;
}

.footer_social{
    display: flex;
    justify-content: center;
    grid-column-gap: 1.2rem;
    -webkit-column-gap: 1.2rem;
            column-gap: 1.2rem;
}

.footer_social-icon{
    background-color: rgb(92, 113, 138);
    color: rgb(17, 21, 26);
    font-size: 1.5rem;
    padding: 0.7rem;
    border-radius: 1.5rem;
    display: inline-flex;
}

.footer_social-icon:hover{
    background-color: white;
    transition: .3s;
}

.footer_copy{
    display: block;
    margin-top: 2.2rem;
    text-align: center;
    font-size: 1rem;
}




@media screen and (max-width: 992px){
    .footer_social-icon{
        padding: 0.5rem;
        border-radius: 1rem;
        font-size: 1rem;
    }
  }
  
